import React from 'react';
import './link.css';

interface LinkProps {
    /**
     * Link 텍스트 설정 
     */
    text: string;

    /**
     * Link style 정의
     */
    style?: object;

    /**
     * Link 클릭 시 이동할 링크 정의
     */
}

export const Link = ({
    text,
    style,
    ...props
}) => {
    
    return <div style={style}>
        { text }
    </div>;
};